.about {
  min-height: 100vh;
  position: relative;
}

.about .line-styling {
  position: absolute;
  top: 50px;
  left: 50%;
  transform: translateX(-50%);
  height: 50px;
  width: 85%;

  display: flex;
  align-items: center;
  justify-content: space-between;
}

.about .line-styling .style-circle {
  width: 10px;
  height: 10px;
  border-radius: 50%;
}
.about .line-styling .style-line {
  width: 95%;
  height: 5px;
  border-radius: 10px;
}

.about-body {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  padding-top: 10rem;
  height: fit-content;
}

.about-description {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-evenly;
  flex: 0.5;
  width: 100%;
  height: fit-content;
}

.about-description > h2 {
  margin-bottom: 2.5rem;
  font-size: 3.5rem;
  font-family: var(--primaryFont);
}

.about-description > p {
  font-size: 1.25rem;
  color: #e0d9d9;
  font-family: var(--primaryFont);
}

.about-img {
  margin-top: 2.5rem;
  pointer-events: none;
}

.about-img > img {
  width: 380px;
  flex: 0.5;
}

@media (min-width: 920px) and (orientation: portrait) {
  .about {
    min-height: unset;
    padding-top: 5rem;
    padding-bottom: 5rem;
  }
}

@media (max-width: 920px) and (orientation: portrait) {
  .about {
    min-height: unset;
    /* padding-top: 5rem; */
    padding-bottom: 5rem;
  }
}

@media (min-width: 992px) and (max-width: 1380px) {
  /* .about {
    min-height: 85vh;
  } */
  /* .about-body {
    padding-top: 15rem; 
  } */
  .about-description {
    flex: 0.7 1;
  }
  .about .line-styling {
    width: 90%;
  }
  /* .about-description > h2 {
    font-size: 3.3rem;
    margin-bottom: 30px;
  } */
  /* .about-description > p {
    font-size: 1.15rem;
  } */

  /* .about-img > img {
    width: 300px;
  } */
}

@media (max-width: 1100px) {
  .about .line-styling {
    width: 95%;
  }
  .about-description {
    flex: 0.8 1;
  }
  .about-description > p {
    font-size: 1.1rem;
  }
}

@media screen and (max-width: 992px) {
  .about .line-styling {
    top: 20px;
    width: 85%;
  }

  .about-body {
    padding-top: 2rem;
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    justify-content: space-evenly;
  }

  .about-description {
    flex: 1;
    /* box-sizing: border-box; */
    /* padding: 5% 10%; */
    padding: 2rem;
  }

  .about-description > h2 {
    /* font-size: 3rem; */
    margin-bottom: 20px;
  }

  .about-description > p {
    font-size: 1rem;
  }

  .about-img > img {
    width: 250px;
    flex: 0.4;
  }
}

@media (max-width: 920px) {
  .about {
    height: auto;
  }
  .about-img {
    display: none;
  }
  .about-description {
    padding: 5rem 2rem 2rem 2rem;
  }
}

@media screen and (max-width: 800px) {
  .about .line-styling .style-circle {
    width: 8px;
    height: 8px;
    border-radius: 50%;
  }
  .about-description > h2 {
    align-self: center;
  }
}

@media screen and (max-width: 800px) and (orientation: landscape) {
  .about-description > h2 {
    font-size: 2.5rem;
  }
}

@media screen and (max-width: 600px) {
  .about .line-styling .style-circle {
    width: 5px;
    height: 5px;
    border-radius: 50%;
  }
  .about .line-styling .style-line {
    width: 93%;
    height: 3px;
    border-radius: 10px;
  }

  .about-description > h2 {
    font-size: 2.5rem;
  }

  .about-description > p {
    font-size: 0.95rem;
  }

  .about-img > img {
    width: 200px;
  }
}

@media screen and (max-width: 480px) {
  .about-description {
    padding: 4rem 2rem 2rem 2rem;
  }
}
